/* eslint-disable array-callback-return */
import { useEffect, useState } from "react";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";

//Components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import "../../index.css";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';


//dependencies
import Context from "../context/UserContext";
import * as categoryServer from "../../services/category";
import CategoryModal from "./categoryModal";
import { useModal } from "../../hooks/useModal";

import * as videoServer from "../../services/videoServer";


const CategoryList = ({ categories }) => {
  const history = useNavigate();

  const [show, handleShow, handleClose] = useModal(false);
  const [id, setId] = useState("");
  const [currentItem, setCurrentItem] = useState({});
  const [listCategories, setCategories] = useState(categories);
  const initialFormData = {categoria:""};
  const [newData, setFormData] = useState(initialFormData);
  const [videos,setVideos] = useState([])
  const { user } = useContext(Context);

  const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(0),
        marginBottom: theme.spacing(10),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    title1: {
      textAlign: "center",
      color: "white"
    }
  }));
 

  
  const editar = async (item) => {
    setId(item.id);
    setCurrentItem(item);
    setFormData({ categoria: item.categoria });
    handleShow(true)
  };

  const registrar = async () => {
    setId(null);
    setCurrentItem({});
    setFormData({categoria:""});
    handleShow(true)
  };
  


  const getCategorias = async () => {
    try {
      const res = await categoryServer.ListCategorias();
      const data = await res;
      setCategories(data);
    } catch (error) {
      console.log(error);
    }
  };
  const getVideosByCategory = async ()=>{

    try {
      const res = await videoServer.ListVideos();
      const data = await res;
      setVideos(data.videos);
    } catch (error) {
      console.log(error);
    }
  }

  useEffect(() => {
   

    if (user) {
      getVideosByCategory()
      getCategorias();
      setId(null)
      
    } else {
    history("/loginuser");
      
    }
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const classes = useStyles();
  
  if (user) {
    
    return (
      <Container> 
        <div className={classes.paper}>
              <h1 className="mt-4">Categorías</h1>
        </div>
        <div  className="d-flex justify-content-between">
          <div>
          <span className="border border-light p-2 rounded text-light">{videos.length} Videos</span>
          </div>
          <div>

          <Button 
          variant="contained" 
          color="success"
          type="submit"
          onClick={()=>registrar()}>
            Registrar Categoría
          </Button>
            </div>
        </div>
        <br/><br/> <br/>     
        <div>
          <section className="content">
          <div className="table-responsive table-condensed table-sm tabla">
            <table
              className="table table-hover"
              id="dataTableData"
              name="dataTableData"
              style={{ fontSize: 15, textAlign: "center" }}
            >
              <thead className={classes.title1}>
                <tr>
                  <th style={{ display: "none" }}>Id</th>
                  <th>Codigo</th>
                  <th>Categoría</th>
                  <th>Cantidad de videos</th>
                  <th></th>
                </tr>
              </thead>
              <tbody style={{ backgroundColor: "white" }}>
                {listCategories.length === 0 ? (
                  <tr>
                    <td colSpan={11}>No hay datos</td>
                  </tr>
                ) : (
                  listCategories.map((Categoria, index) => (
                    <tr key={index}>
                      <td style={{ display: "none" }}>{Categoria.id}</td>
                      <td>{Categoria.id}</td>
                      <td>{Categoria.categoria}</td>
                      <td>{videos.filter((video) => (
      video.categorias.some((element) => Categoria.categoria === element.categoria))).length}</td>
                      <td>
                        <button
                          className="btn btn-outline-primary p-1 btn-sm float-right"
                          type="submit"
                          onClick={()=>editar(Categoria)}
                        >
                          editar
                        </button>
                       
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </section>
        </div>
        <CategoryModal
          handleClose={handleClose}
          show={show}
          category_id ={id}
          currentItem={currentItem}
          setCategories = {setCategories}
          setFormData = {setFormData}
          newData={newData}
        ></CategoryModal>
      </Container>
    );
  }
  return (
    <p style={{ fontSize: "25px" }}>Inicia sesión para ver todos los videos!</p>
  );
};

export default CategoryList;
